*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root, body {
  width: 100vw;
}

:root {
  font-size: calc(1em + .2vw);
}

body {
  --_primary_color: #064724;
  --_secondary_color: #d2a4b1;
  --_control_item_color: #a26374;
  --_move_icon_url: url("move.ad5f426f.svg");
  --_move_bright_icon_url: url("move_bright.c643830e.svg");
  --_back_bright_icon_url: url("back01_bright.3432b3cd.svg");
  --_plus_bright_icon_url: url("plus_bright.27ffb631.svg");
  --_minus_bright_icon_url: url("minus_bright.54c0a730.svg");
  --_show_tag_icon_url: url("show_tag.c4bcfd29.png");
  --_hide_tag_icon_url: url("hide_tag.3cac484e.png");
  --_one_row_icon_url: url("one_row.63e50900.png");
  --_two_row_icon_url: url("two_row.704dff04.png");
  background-color: var(--_primary_color);
}

main {
  background-color: var(--_secondary_color);
  flex-direction: column;
  width: 100%;
  height: 100vh;
  display: flex;
}

.controlbar {
  color: #eee;
  background-color: var(--_primary_color);
  flex-grow: 0;
  place-content: space-between start;
  width: 100%;
  height: -moz-fit-content;
  height: fit-content;
  padding: .3em .3em .5em .5em;
  font-size: 1rem;
  display: flex;
}

.control_item_group {
  place-content: space-between start;
  width: 100%;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
  overflow: scroll hidden;
}

.control_item {
  background-color: var(--_control_item_color);
  cursor: pointer;
  border: #0000;
  border-radius: .3em;
  margin-right: 1em;
  padding: .75em 1em;
  font-size: 1em;
}

.control_item[action="go_back"] {
  background-image: var(--_back_bright_icon_url);
  background-color: #0000;
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto 80%;
  margin-right: .5em;
  display: block;
}

.control_item[action="move_keyboard"] {
  background-image: var(--_move_bright_icon_url);
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto 80%;
}

.control_item[action="zoom_out_keyboard"] {
  background-image: var(--_plus_bright_icon_url);
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto 80%;
}

.control_item[action="zoom_in_keyboard"] {
  background-image: var(--_minus_bright_icon_url);
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto 80%;
}

.control_item[action="switch_pitch_tag"] {
  background-image: var(--_show_tag_icon_url);
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto 80%;
}

.control_item[action="switch_keyboard_row"] {
  background-image: var(--_one_row_icon_url);
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto 80%;
}

.control_item[action="switch_keyboard_row"][two_row] {
  background-image: var(--_two_row_icon_url);
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto 80%;
}

.keyboards_container {
  flex-direction: column;
  flex-grow: 1;
  width: 100%;
  height: 100%;
  display: flex;
}

.keyboards_container > :last-child {
  display: none;
}

.keyboards_container[two_row] > :last-child {
  display: block;
}

.scroll_keyboard_component {
  --keyboard-width: 200vw;
  width: 100%;
  height: 100%;
  position: relative;
}

.scroll_keyboard_component[enable_scroll]:after {
  content: "";
  background-image: var(--_move_icon_url);
  opacity: .5;
  pointer-events: none;
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto 50%;
  position: absolute;
  inset: 0;
}

.scrollable_container {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.scroll_keyboard_component[enable_scroll] .scrollable_container {
  overflow-x: scroll;
}

.keyboard_container {
  width: var(--keyboard-width);
  height: 100%;
  margin: 0 auto;
  position: relative;
}

.scroll_keyboard_component[enable_scroll] .keyboard_container:after {
  content: "";
  background-color: #01063233;
  position: absolute;
  inset: 0;
}

.piano-keyboard {
  width: 100%;
  height: 100%;
  position: relative;
}

